@font-face {
    font-family: josefinsans;
    src: url(../fonts/josefsans/JosefinSans-Regular.ttf);
  }
@font-face {
    font-family: karla;
    src: url(../fonts/karla/Karla-Regular.ttf);
  }
*
{
    margin: 0;
    padding: 0;
}

html
{
   background-color: #2b2b2b;
}
body
{
    overflow: hidden;
    background-color: #2b2b2b;

}

.webgl
{
    position: fixed;
    background-color: white;
    color: white;
    top: 0;
    left: 0;
    outline: none;
    display: block;
    width: 100%;
    height: 100%;
}

.sec_1{
  position: absolute;
    text-align: center;
    justify-content: center;
    /*! responsive */
    top: 20%;
    left: 10%;
   
   
   
}


h1{
    font-size: 2vw;
    color: white;
    font-family: josefinsans;
}
.p_sec{
  font-family: karla;
  font-size: 1vw; color: black;
}

  .btn_1{
    background-color: #abddf1;
    color: #3b3b3b;
    margin-top: 4%;
    width: 10vw;
    height: 2.5vw;
    border-radius: 25px;
    border: 0px;
    font-size: 1vw;
    text-align: center;
    font-family: karla ;
    cursor: pointer;

  }
  .self_hint {
    display: flex;
    flex-direction: column;
    color: "#ffffff";
    background-color: black;
    opacity: 0.95;
    border-top-left-radius: 40px;
    border-bottom-right-radius: 40px;
    position: absolute;
    top: 20%;
    left: 0;
    right: 0;
    margin: auto;
    align-items: center;
    width: 25%;
    height: auto;
    font-size: 1rem;
    font-family: karla;
      text-align: center;
    justify-content: center;
    color: white;
  
   }
   .socialmediaDiv{
    display: none;
    color: "#ffffff";
    background-color: black;
    
    opacity: 0.85;
    border-top-left-radius: 40px;
    border-bottom-right-radius: 40px;
    position: absolute;
    top: 40%;
    left: 25%;
    align-items: center;
    width: 16rem;
    height: auto;
    flex-direction: column;
    font-size: 1rem;
    font-family: karla;
      text-align: center;
    justify-content: center;
    color: white;
   }
  @media screen and (max-width: 464px )  {
   
         h1{
          font-size: 6vw;
         }
        .p_sec{
          font-size: 3vw
         }
        .btn_1{
          width:30vw;
