          font-size: 3vw;

        }
        .sec_1{
          top:40%;
          left:0;
          right: 0;
        }
    }
  @media screen and (max-width: 1400px )  {
         
      .btn_1{
        margin-top: 0;
      }
        .sec_1{
          top:40%;
          left:0;
          right: 0;
        }
    }
 

  .btn_1:hover{
    background-color: white;
