
.sec_2{
    float: right; 
    margin-top: 20%;
    margin-right: 15%;
}
.Hamberger_div{
    float: right;
    position: relative;
    flex-direction: row;
}

.i_hamberger{
    float: right;
     margin-top:20px;
     margin-right: 15px;
     cursor: pointer;


}
.i_hamberger:hover{
    opacity: 0.4;
    
}

.hover_div{
    margin-top: -70%;
    background-color: #bd791f;
    flex-direction: column;
    animation-duration: 0.7s;
    animation-fill-mode: forwards;
    vertical-align: middle;
    justify-content: center;
    justify-items: center;
    visibility: hidden;
    color: white;


}

.list_menu{
    display: flex;
    flex-direction: column;
    justify-content: center;
    justify-items: center;
    text-align: center;
    font-size: 35px;
    gap: 80px;
    font-family: karla;
    grid-template-columns: auto auto auto auto;
    
}

@keyframes DivHover {
    from{ width: 0px; height: 0px;}
    to{width: 300px; height: 600px;}
    
}
@keyframes UnDivHover {
    from{ width: 350px; height: 600px;}
    to{width: 300px; height: 0px;}
    
}

a:hover{
    opacity: 0.4;
    cursor: pointer;
}
